<template>
  <!-- 弹窗 -->
  <VanPopup
    class="components-payTypeDialog"
    v-model="payDialogConfig.dialogMark"
    @click-close-icon="$emit('clickCloseIcon')"
    round
    position="bottom"
    closeable
    close-icon="close"
    :style="{ height:'30%' }">
    <div class="title">请选择支付方式</div>
    <div class="icon-box">
      <img
        @click="payTypeClick(item.key)"
        :src="item.img"
        :class="['img-box', { 'ml__130': index }]"
        v-for="(item, index) in iconList"
        :key="index" />
    </div>
  </VanPopup>
</template>
<script>
export default {
  name: 'PayTypeDialog',
  props: {
    payDialogConfig: {
      type: Object,
      default: () => ({
        dialogMark: false
      })
    }
  },
  data () {
    return {
      iconList: [
        // 图标列表
        {
          img: require('@/assets/baiduAB/aliPay.png'),
          key: '1'
        },
        {
          img: require('@/assets/baiduAB/wxPay.png'),
          key: '2'
        }
      ]
    }
  },
  methods: {
    /**
     * @Date: 2021-01-16 13:55:48
     * @author: zjs
     * @description: 支付类型被点击
     */
    payTypeClick (key) {
      this.$emit('payTypeClick', key)
      this.$nextTick(() => {
        this.payDialogConfig.dialogMark = false
      })
    }
  }
}
</script>
<style lang="scss">
.components-payTypeDialog {
  .title {
    margin-top: 48px;
    font-size: 38px;
    font-weight: 600;
    color: #333;
    text-align: center;
  }

  .icon-box {
    margin-top: 36px;
    text-align: center;
  }

  img {
    width: 200px;
    height: 200px;
  }

  .ml__130 {
    margin-left: 130px;
  }
}
</style>
