
import { clickLog } from '@/helper/clickLog'
import { Dialog } from 'vant'
import { isBuyOrder } from './payMent'
import { isPhone } from '@/utils'

/**
 * @Date: 2020-12-28 18:48:13
 * @author: zjs
 * @description: 打开支付类型弹窗
 */
export async function openPayTypeDialog () {
  try {
    const { phone, code } = this
    const msg = phone ? '手机号码有误，请重填' : '请填写手机号'
    if (!isPhone(phone)) {
      Dialog({ message: msg })
      clickLog({
        module_ori: '表单错误模块',
        module_part: this.ERROR_PHONE
      })
      return
    }
    await this.$apis.userOperator.phoneCheckCode({
      phone,
      code
    })
    await isBuyOrder(phone)
    this.payDialogConfig.dialogMark = true
  } catch (error) {
    if (error.code === 33002) {
      clickLog({
        module_ori: '表单错误模块',
        module_part: this.ERROR_CODE
      })
    }
  }
}
/**
 * @Date: 2020-12-30 10:27:08
 * @author: zjs
 * @description: 支付类型被点击
 */
export function payTypeClick (type) {
  this.paytype = type
  const isWx = String(this.paytype) === '2'
  clickLog({
    module_ori: '支付选择模块',
    module_part: isWx ? this.WX_BTN : this.ALI_BTN
  })
}
/**
 * @Date: 2020-12-31 14:13:13
 * @author: zw
 * @description: 关闭支付类型弹窗
 */
export function closePayTypeDialog () {
  clickLog({
    module_ori: '支付选择模块',
    module_part: this.CLOSE_BTN
  })
  this.payDialogConfig.dialogMark = false
}
